.bookPreview{
    transition: 0.25s;
    margin-bottom: 2vh;
}
.bookPreview:hover{
    -webkit-box-shadow: 5px 5px 15px -6px #000000; 
    box-shadow: 5px 5px 15px -6px #000000;
    -moz-box-shadow: 5px 5px 15px -6px #000000;
    scale: 101%;
    cursor: pointer;
}
.bookPreviewTextName{
    text-align: center;
    font-size: x-large;
    padding-top: 0.3vh;
    padding-bottom: 0.3vh;
}
.bookPreviewTextAuthor{
    text-align: center;
    padding-bottom: 1.6vh;
}
.bookPreviewTextRating{
    text-align: center;
    padding-bottom: 1vh;
    font-size: larger;
}
.bookPreviewTextSynopsis{
    text-align: center;
}